<template>
  <div>
    <div class="container">
      <account-main></account-main>
      <v-divider></v-divider>
      <account-profile></account-profile>
      <v-divider></v-divider>
      <account-chatbot></account-chatbot>
      <v-divider></v-divider>
      <account-mail></account-mail>
    </div>
  </div>
</template>
<script>
import _ from 'lodash'
import AccountMain from './account/AccountMain'
import AccountProfile from './account/AccountProfile'
import AccountChatbot from './account/AccountChatbot'
import AccountMail from './account/AccountMail'
import API from '@/services/API'
import firebase from 'firebase'
import {mapMutations} from 'vuex'
export default {
  name: 'account',
  components: {
    AccountMain,
    AccountProfile,
    AccountChatbot,
    AccountMail
  },
  data: function data () {
    return {
      uid: null,
      usname: null,
      name: null,
      lname: null,
      email: null,
      profilgame: false,
      profildev: false,
      checked: false,
      password: '',
      message: '',
      invalide: false,
      valide: false,
      ...this.$store.state.user
    }
  },
  computed: {
    isDisabled () {
      return _.isEqual(this.newUser, this.initialUser) // initial user is not updated, but user is
    },
    initialUser () {
      const user = this.$store.state.user
      return {
        uid: user.uid || null,
        usname: user.usname || null,
        name: user.name || null,
        lname: user.lname || null,
        email: user.email || null,
        profilgame: user.profilgame || false,
        profildev: user.profildev || false,
        city: user.city || null,
        vename: user.vename || null
      }
    },
    newUser () {
      return {
        uid: this.uid,
        usname: this.usname,
        name: this.name,
        lname: this.lname,
        email: this.email,
        casej: this.profilgame,
        casedev: this.profildev,
        password: this.password,
        city: this.city,
        vename: this.vename,
      }
    }
  },
  methods: {
    ...mapMutations(['valideUser']),
    onUpdate: function (e) {
      let $this = this
      API.user.update(this.newUser, this).then((user) => {
        // go back to the chat interface
        this.$router.push({ name: 'Chat' })
      }).catch(function (error) {
        $this.invalide = true
        $this.message = error
        console.log('error',error)
      })
    }
  },
  mounted () {
    this.$store.commit('updateColor', this.$store.state.color);
    let $this = this
    firebase.auth().onAuthStateChanged(function (user) {
      if (user) {
        // store.commit('updateUserId', user.uid)
      } else {
        const userId = 123
//        $this.$router.push({ name: 'Signup', params: { userValitde: false }})
        $this.$router.push({name: 'Signup', params: { userId }})
        $this.valideUser(false)
      }
    })
  }
}
</script>
<style lang="scss">
.flex{

  :first-child{
    margin-right: 8px;
  }
  :not(:first-child){
    margin-left: 8px;
  }
  .input-100{
    width: 100%;
    margin-right: inherit;
    input {
      width: 100%;
    }

  }
  input[type=checkbox] {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;

  }
  input[type=checkbox] {
    -border-radius: 4px;
    height: 30px;
    width: 30px;
    background: linear-gradient(70deg, rgba(#aaaaaa, 0.5), rgba(#999999, 0.5));
    border: 1px solid #ccc;
  }
  input[type="checkbox"]:checked {
    background: linear-gradient(70deg, rgba(#aaaaaa, 0.5), rgba(#999999, 0.5));
    /*margin:0px;*/
    position: relative;
    &:before {
      font-family: FontAwesome;
      content: '\f00c';
      display: block;
      color: grey;
      font-size: 13px;
      position: absolute;
    }
  }
  label {
    position: relative;
    bottom: 28px;
  }

}
.flex2{
  margin-top: 65px

}
.divider{
  background: linear-gradient(70deg,hsla(0,0%,67%,.5),hsla(0,0%,60%,.5));
  padding-top: 10px;
  width: 100%;
  margin-top: 50px;
  border-radius: 5px;
}
</style>



