/**
 * Requests related to the interface gamification, experience for example
 */
import Axios from './Axios'
import store from '@/config/vuex/store'
import localStorageHelpers from '@/helpers/localStorageHelpers'
import { auth, db } from '@/config/firebase'
import firebase from 'firebase'

// TODO : remove this when using the real API
let exp = 0
let node_count = 0
export const emergence = {
  /**
  * Get the current user experience
  * @return {*} [description]
  */
  // getExp: () => {
  //   return new Promise((resolve, reject) => {
  //     exp = (exp + 5)
  //     const newExp = {
  //       current: exp % 100,
  //       total: 100,
  //       level: Math.floor(exp / 100)
  //     }
  //     localStorageHelpers.setJSONItem('exp', newExp)
  //     store.commit('updateExp', newExp)
  //     resolve(newExp)
  //   })
  // },
  getExp: () => ( new Promise((resolve, reject) => {
      let newExp = {
        current: 0,
        total: 100,
        level: 0
      }
      console.log(store.state.user.uid)
      //Users
              db.ref('users')
              .orderByChild('email')
                  .equalTo(store.state.user.email)
                  .on('value', snapshot => {
                      snapshot.forEach(function(profile) {
                        // console.log('length')
                        // console.log(Object.keys(profile.val()).length)
                        node_count = Object.keys(profile.val()).length
                        exp = (node_count-9) * 5
                        newExp = {
                          current: exp % 100,
                          total: 100,
                          level: Math.floor(exp / 100)
                        }
                        localStorageHelpers.setJSONItem('exp', newExp)
                        store.commit('updateExp', newExp)
                        // resolve(newExp)
                      })
                  })
                  db.ref('altaia01').orderByKey().equalTo(store.state.user.uid)
                      .on('value', snapshot => {
                          snapshot.forEach(function(profile) {
                            // console.log(profile)
                            console.log('altaia01')
                            // console.log(Object.keys(profile.val()).length)
                            node_count = Object.keys(profile.val()).length
                            exp = (newExp.current/5+(newExp.level*20)+node_count-1) * 5
                            // console.log('exp')
                            // console.log(newExp.current,newExp.level, node_count, exp)
                            newExp = {
                              current: exp % 100,
                              total: 100,
                              level: Math.floor(exp / 100)
                            }
                            localStorageHelpers.setJSONItem('exp', newExp)
                            store.commit('updateExp', newExp)
                            // resolve(newExp)
                          })
                      })
                      db.ref('family01').orderByKey().equalTo(store.state.user.uid)
                          .on('value', snapshot => {
                              snapshot.forEach(function(profile) {
                                // console.log(profile)
                                console.log('family01')
                                // console.log(Object.keys(profile.val()).length)
                                node_count = Object.keys(profile.val()).length
                                exp = (newExp.current/5+(newExp.level*20)+node_count-1) * 5
                                // console.log('exp')
                                // console.log(newExp.current,newExp.level, node_count, exp)
                                newExp = {
                                  current: exp % 100,
                                  total: 100,
                                  level: Math.floor(exp / 100)
                                }
                                localStorageHelpers.setJSONItem('exp', newExp)
                                store.commit('updateExp', newExp)
                                // resolve(newExp)
                            })
                        })
                        db.ref('music01').orderByKey().equalTo(store.state.user.uid)
                            .on('value', snapshot => {
                                snapshot.forEach(function(profile) {
                                  // console.log(profile)
                                  console.log('music01')
                                  // console.log(Object.keys(profile.val()).length)
                                  node_count = Object.keys(profile.val()).length
                                  exp = (newExp.current/5+(newExp.level*20)+node_count-1) * 5
                                  // console.log('exp')
                                  // console.log(newExp.current,newExp.level, node_count, exp)
                                  newExp = {
                                    current: exp % 100,
                                    total: 100,
                                    level: Math.floor(exp / 100)
                                  }
                                  localStorageHelpers.setJSONItem('exp', newExp)
                                  store.commit('updateExp', newExp)
                                  // resolve(newExp)
                            })
                        })
                        db.ref('videog01').orderByKey().equalTo(store.state.user.uid)
                            .on('value', snapshot => {
                                snapshot.forEach(function(profile) {
                                  // console.log(profile)
                                  console.log('videog01')
                                  // console.log(Object.keys(profile.val()).length)
                                  node_count = Object.keys(profile.val()).length
                                  exp = (newExp.current/5+(newExp.level*20)+node_count-1) * 5
                                  // console.log('exp')
                                  // console.log(newExp.current,newExp.level, node_count, exp)
                                  newExp = {
                                    current: exp % 100,
                                    total: 100,
                                    level: Math.floor(exp / 100)
                                  }
                                  localStorageHelpers.setJSONItem('exp', newExp)
                                  store.commit('updateExp', newExp)
                                  // resolve(newExp)
                          })
                      })
                      resolve(newExp)
    })
  ),
  _getExp: () => {
    return Axios.get('/experience')
    .then((res) => {
      return res.data
    })
  }
}

export default emergence



