<template>
  <form v-on:submit.prevent="onUpdate($event)" id="account-invitemail" class="vertical big">
    <div class="flex2">
    <div class="input-100" style="color: red" v-if="invalide">{{message}}</div>
    <div  style="color: green; height: 20px" v-if="valide">{{$t('models.user.changepassword')}}</div>
    </div>
    <div class="flex">
      <h2>
        {{$t('components.user.account.sendInvitationToFriends')}}
      </h2>
    </div>
    <div class="flex">
      <div class="input-100">
        <input type="email" name="email1"  v-model="email1" :placeholder="$t('models.user.email1')" >
      </div>
    </div>
    <div class="flex">
      <div class="input-100">
        <input type="email" name="email2"  autocomplete="off" v-model="email2" :placeholder="$t('models.user.email2')">
      </div>
    </div>
    <div class="flex">
      <div class="input-100">
        <input type="email" name="email3"  autocomplete="off" v-model="email3" :placeholder="$t('models.user.email3')">
      </div>
    </div>
    <div class="button-wrapper">
      <altai-button type="submit" :disabled="isDisabled">{{$t('components.user.account.inviteButton')}}</altai-button>
    </div>
</form>
</template>
<script>
import _ from 'lodash'
import AltaiButton from '@/components/ui/AltaiButton'
import API from '@/services/API'
import firebase from 'firebase'
import {mapMutations} from 'vuex'
export default {
  name: 'account-mail',
  components: {
    AltaiButton
  },
  data: function data () {
    return {
      uid: null,
      email1: null,
      email2: null,
      email3: null,
      message: '',
      invalide: false,
      valide: false,
      ...this.$store.state.user
    }
  },
  computed: {
    isDisabled () {
      return _.isEqual(this.newUser, this.initialUser) // initial user is not updated, but user is
    },
    initialUser () {
      const user = this.$store.state.user
      return {
        email1: user.email1 || null,
        email2: user.email2 || null,
        email3: user.email3 || null,
      }
    },
    newUser () {
      return {
        email1: this.email1,
        email2: this.email2,
        email3: this.email3
      }
    }
  },
  methods: {
    ...mapMutations(['valideUser']),
    onUpdate: function (e) {
      let $this = this
      API.user.update(this.newUser, this).then((user) => {
        // go back to the chat interface
        this.$router.push({ name: 'Chat' })
      }).catch(function (error) {
        $this.invalide = true
        $this.message = error
        console.log('error',error)
      })
    }
  },
  mounted () {
    let $this = this
    firebase.auth().onAuthStateChanged(function (user) {
      if (user) {
        // store.commit('updateUserId', user.uid)
      } else {
        const userId = 123
//        $this.$router.push({ name: 'Signup', params: { userValitde: false }})
        $this.$router.push({name: 'Signup', params: { userId }})
        $this.valideUser(false)
      }
    })
  }
}
</script>
<style lang="scss">
.flex{

  :first-child{
    margin-right: 8px;
  }
  :not(:first-child){
    margin-left: 8px;
  }
  .input-100{
    width: 100%;
    margin-right: inherit;
    input {
      width: 100%;
    }

  }
  input[type=checkbox] {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;

  }
  input[type=checkbox] {
    -border-radius: 4px;
    height: 30px;
    width: 30px;
    background: linear-gradient(70deg, rgba(#aaaaaa, 0.5), rgba(#999999, 0.5));
    border: 1px solid #ccc;
  }
  input[type="checkbox"]:checked {
    background: linear-gradient(70deg, rgba(#aaaaaa, 0.5), rgba(#999999, 0.5));
    /*margin:0px;*/
    position: relative;
    &:before {
      font-family: FontAwesome;
      content: '\f00c';
      display: block;
      color: grey;
      font-size: 13px;
      position: absolute;
    }
  }
  label {
    position: relative;
    bottom: 28px;
  }

}
.flex2{
  margin-top: 65px

}
</style>



