/**
 * Requests related to the interface gamification, experience for example
 */
import fakeData from '@/helpers/fakeData'
import Axios from './Axios'
import moment from 'moment/min/moment-with-locales'
// import faker from 'faker'
import localStorageHelpers from '@/helpers/localStorageHelpers'
import store from '@/config/vuex/store'
import { auth, db } from '@/config/firebase'
import firebase from 'firebase'

let shop_path = '/en'
export const shop = {
  getCategories: () => {
    return new Promise((resolve, reject) => {
      let userLang = navigator.language || navigator.userLanguage
      if(userLang == 'en-US'){
        shop_path = '/en'
      }else{
        shop_path = '/fr'
      }
      let categories = []
      db.ref('shop'+shop_path)
        .on('value', snapshot => {
          snapshot.forEach(function(category){
            const category_item = {
              id : category.key,
              title : category.val().title,
              bgColor : category.val().bg_color,
              icon : category.val().icon
            }
            categories.push(category_item)
          })
        })
      resolve(categories)
    })
  },
  getCategory: (categoryId) => new Promise((resolve, reject) => {
    let category_item
    let userLang = navigator.language || navigator.userLanguage
    if(userLang == 'en-US'){
      shop_path = '/en'
    }else{
      shop_path = '/fr'
    }
    console.log('lang', userLang, shop_path);
    db.ref('shop'+shop_path).orderByKey().equalTo(categoryId)
      .on('value', snapshot => {
        snapshot.forEach(function(category){
          category_item = {
              id : category.key,
              title : category.val().title,
              bgColor : category.val().bg_color,
              icon : category.val().icon
            }
          })
        resolve(category_item)
      })
  }),
  getItems: (categoryId) => new Promise((resolve, reject) => {
    let items = []
    let userLang = navigator.language || navigator.userLanguage
    if(userLang == 'en-US'){
      shop_path = '/en'
    }else{
      shop_path = '/fr'
    }
    db.ref('shop'+shop_path).orderByKey().equalTo(categoryId)
      .on('value', snapshot => {
        snapshot.forEach(function(category){
          const category_items = category.val().items
          const keys = Object.keys(category_items)
          for(let i=0; i<keys.length; i++){
            let item = category_items[keys[i]]
            const category_item = {
              id : keys[i],
              title : item.title.replace(/\$vename/g, store.state.user.vename),
              shortDescription : item.short_desc.replace(/\$vename/g, store.state.user.vename),
              icon : '',
              price: item.price,
              categoryId : categoryId
            }
            items.push(category_item)
          }
        })
      })
    resolve(items)
  }),
  getItem: (categoryId, itemId) => new Promise((resolve, reject) => {
    let category_item
    let userLang = navigator.language || navigator.userLanguage
    if(userLang == 'en-US'){
      shop_path = 'en/'
    }else{
      shop_path = 'fr/'
    }
    db.ref('shop/'+shop_path+categoryId+'/items').orderByKey().equalTo(itemId)
      .on('value', snapshot => {
        snapshot.forEach(function(item){
          category_item = {
              id : item.key,
              title : item.val().title.replace('$vename', store.state.user.vename),
              shortDescription : item.val().short_desc.replace(/\$vename/g, store.state.user.vename),
              longDescription : item.val().content.replace(/\$vename/g, store.state.user.vename),
              icon : '',
              price: item.val().price,
              categoryId: categoryId
            }
        })
        resolve(category_item)
      })
  }),
  addItem: (categoryId, item) => new Promise((resolve, reject) => {
    console.log('addItem', categoryId, item);
    let item_categoryId = item?item.categoryId:categoryId.categoryId
    let id = item?item.id:categoryId.id
    db.ref('shopcart').child(store.state.user.uid).child(item_categoryId).child(id).set({
      time : moment().format('MM/DD/YYYY HH:mm'),
      itemId : id
    })
    resolve()

  }),
  removeItem: (item) => new Promise((resolve, reject) => {
    console.log('category', item.categoryId);
    db.ref('shopcart').child(store.state.user.uid).child(item.categoryId).child(item.id).remove()
    resolve()
  }),
  emptyCart: () => new Promise((resolve, reject) => {
    resolve()
  }),
  getCartItems: () => new Promise((resolve, reject) => {
    let items = []
    let category_item
    let userLang = navigator.language || navigator.userLanguage
    if(userLang == 'en-US'){
      shop_path = 'en/'
    }else{
      shop_path = 'fr/'
    }
    db.ref('shopcart').orderByKey().equalTo(store.state.user.uid)
      .once('value', snapshot => {
        snapshot.forEach(function(category){
          let categories = category.val()
          let item_keys = Object.keys(categories)
          for(let j=0; j<item_keys.length; j++){
            let keys = Object.keys(categories[item_keys[j]])
            for(let i=0; i<Object.keys(keys).length; i++){
              console.log(Object.keys(categories[item_keys[j]]))
              db.ref('shop/'+shop_path+item_keys[j]+'/items').orderByKey().equalTo(keys[i])
              .on('value', snapshot=>{

                snapshot.forEach(function(item){
                  category_item = {
                    id : item.key,
                    title : item.val().title.replace(/\$vename/g, store.state.user.vename),
                    shortDescription : item.val().short_desc.replace(/\$vename/g, store.state.user.vename),
                    icon : '',
                    price: item.val().price,
                    categoryId: item_keys[j]
                  }
                  items.push(category_item)
                })
              })
            }
          }
            resolve(items)
        })
      })
  })
}

export default shop



