<template>
  <form v-on:submit.prevent="onUpdate($event)" id="account-chatbot" class="vertical big">
    <div class="flex2">
    <div class="input-100" style="color: red" v-if="invalide">{{message}}</div>
    <div  style="color: green; height: 20px" v-if="valide">{{$t('models.user.changepassword')}}</div>
    </div>
    <div class="flex">
      <div>
        <input type="text" readonly name="vename" v-model="vename" :placeholder="$t('models.user.veName')" >
      </div>
    </div>
    <div class="flex">
      <div>
        <input type="text" readonly name="creadat"  v-model="creadat"  :placeholder="$t('models.user.dateOfAdopt')">
      </div>
    </div>
    <div class="flex flex3">
      <div style="width:30%">
        <h3>{{$t('models.user.humor')}}</h3>
      </div>
      <div  style="width:70%">
        <vue-slider  @callback="cb" ref="slider4" v-bind="humor_slider" v-model="humor"></vue-slider>
      </div>
    </div>
    <div class="flex flex3">
      <div style="width:30%">
        <h3>{{$t('models.user.chatter')}}</h3>
      </div>
      <div  style="width:70%">
        <vue-slider  @callback="cb" ref="slider5" v-bind="humor_slider" v-model="chatter"></vue-slider>
      </div>
    </div>
    <div class="flex flex3">
      <div style="width:30%">
        <h3>{{$t('models.user.curFeeling')}}</h3>
      </div>
      <div  style="width:70%">
        <vue-slider  @callback="cb" ref="slider6" v-bind="curFeeling_slider" v-model="curFeeling"></vue-slider>
      </div>
    </div>
    <div class="flex flex3">
      <div style="width:30%">
        <h3>{{$t('models.user.curEmotionalState')}}</h3>
      </div>
      <div  style="width:70%">
        <vue-slider  @callback="cb" ref="slider7" v-bind="curEmotionalState_slider" v-model="curEmotionalState"></vue-slider>
      </div>
    </div>
</form>
</template>
<script>
import _ from 'lodash'
import AltaiButton from '@/components/ui/AltaiButton'
import API from '@/services/API'
import firebase from 'firebase'
import {mapMutations} from 'vuex'
import vueSlider from 'vue-slider-component';
import moment from 'moment/min/moment-with-locales'
export default {
  name: 'account-chatbot',
  components: {
    AltaiButton,
    vueSlider
  },
  data: function data () {
    return {
      uid: null,
      vename: null,
      creadat: null,
      humor: 0,
      chatter: null,
      curFeeling: null,
      curEmotionalState: null,
      message: '',
      invalide: false,
      valide: false,
      style:{
        width: '100%',
        height: 8,
        dotSize: 20,
        min: 1,
        max: 3,
        interval: 1,
        disabled: false,
        show: true,
        tooltip: 'always',
        piecewise: true,
        style: {
          marginBottom: '30px'
        },
        bgStyle: {
          backgroundColor: '#fff',
          boxShadow: 'inset 0.5px 0.5px 3px 1px rgba(0,0,0,.36)'
        },
        sliderStyle: [{
          backgroundColor: '#f05b72'
        }, {
          backgroundColor: '#3498db'
        }],
        tooltipStyle: [{
          backgroundColor: '#f05b72',
          borderColor: '#f05b72'
        }, {
          backgroundColor: '#3498db',
          borderColor: '#3498db'
        }],
        processStyle: {
          backgroundImage: '-webkit-linear-gradient(left, #f05b72, #3498db)'
        }
      },
      humor_slider: {
        data: [
          'minimum',
          'medium',
          'maximum'
        ]

      },
      curFeeling_slider: {
        data: [
          'sad',
          'happy',
          'angry'
        ]
      },
      curEmotionalState_slider:{
        data:[
          'apathetic',
          'calm',
          'excited'
        ]
      },
      ...this.$store.state.user
    }
  },
  computed: {
    isDisabled () {
      return _.isEqual(this.newUser, this.initialUser) // initial user is not updated, but user is
    },
    initialUser () {
      const user = this.$store.state.user

      return {
        uid: user.uid || null,
        usname: user.usname || null,
        name: user.name || null,
        lname: user.lname || null,
        email: user.email || null,
        profilgame: user.profilgame || false,
        profildev: user.profildev || false,
        city: user.city || null,
        vename: user.vename || null,
        creadat: user.creadat || null,
        humor: user.humor || 0,
        chatter: user.chatter || 0,
        curFeeling: user.curFeeling || 0,
        curEmotionalState: user.curEmotionalState || 0,
      }
    },
    newUser () {
      return {
        uid: this.uid,
        usname: this.usname,
        name: this.name,
        lname: this.lname,
        email: this.email,
        casej: this.profilgame,
        casedev: this.profildev,
        password: this.password,
        city: this.city,
        vename: this.vename,
      }
    }
  },
  methods: {
    ...mapMutations(['valideUser']),
    onUpdate: function (e) {
      let $this = this
      API.user.update(this.newUser, this).then((user) => {
        // go back to the chat interface
        this.$router.push({ name: 'Chat' })
      }).catch(function (error) {
        $this.invalide = true
        $this.message = error
        console.log('error',error)
      })
    }
  },
  mounted () {
    const slider1 = Object.assign({}, this.curEmotionalState_slider, this.style)
    this.curEmotionalState_slider = slider1
    console.log(this.curEmotionalState_slider)
    console.log('user', this.$store.state.user)

    const slider2 = Object.assign({}, this.humor_slider, this.style)
    this.humor_slider = slider2

    const slider3 = Object.assign({}, this.curFeeling_slider, this.style)
    this.curFeeling_slider = slider3

    this.creadat = moment(this.creadat+"000", "x").format('MM/DD/YYYY HH:mm')

    let $this = this
    firebase.auth().onAuthStateChanged(function (user) {
      if (user) {
        // store.commit('updateUserId', user.uid)
      } else {
        const userId = 123
//        $this.$router.push({ name: 'Signup', params: { userValitde: false }})
        $this.$router.push({name: 'Signup', params: { userId }})
        $this.valideUser(false)
      }
    })
  }
}
</script>
<style lang="scss">
.flex{

  :first-child{
    margin-right: 8px;
  }
  :not(:first-child){
    margin-left: 8px;
  }
  .input-100{
    width: 100%;
    margin-right: inherit;
    input {
      width: 100%;
    }

  }
  input[type=checkbox] {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;

  }
  input[type=checkbox] {
    -border-radius: 4px;
    height: 30px;
    width: 30px;
    background: linear-gradient(70deg, rgba(#aaaaaa, 0.5), rgba(#999999, 0.5));
    border: 1px solid #ccc;
  }
  input[type="checkbox"]:checked {
    background: linear-gradient(70deg, rgba(#aaaaaa, 0.5), rgba(#999999, 0.5));
    /*margin:0px;*/
    position: relative;
    &:before {
      font-family: FontAwesome;
      content: '\f00c';
      display: block;
      color: grey;
      font-size: 13px;
      position: absolute;
    }
  }
  label {
    position: relative;
    bottom: 28px;
  }

}
.flex2{
  margin-top: 65px

}
.flex3{
  margin-bottom: 20px;
  margin-top: 20px;
}
.vue-slider-process{
  margin-left:0 !important;
}
.vue-slider-component{
  line-height: 34px;
  margin-top: 16px;
}
</style>



