<template>
  <form v-on:submit.prevent="onUpdate($event)" id="account-profile" class="vertical big">
    <div class="flex2">
    <div class="input-100" style="color: red" v-if="invalide">{{message}}</div>
    <div  style="color: green; height: 20px" v-if="valide">{{$t('models.user.changepassword')}}</div>
    </div>
    <div class="flex">
      <div class="input-100">
        <input type="text" name="facebook" v-model="facebook" :placeholder="$t('models.user.facebook')" >
      </div>
    </div>
    <div class="flex">
      <div style="width: 50%">
        <v-select v-model="gender" :options="items"></v-select>
      </div>
      <div style="width: 50%">
        <input type="text" name="color" v-model="color" :placeholder="$t('models.user.color')">
      </div>
    </div>
    <div class="flex">
      <div class="input-100">
        <input type="text" name="profession"  autocomplete="off" v-model="profession" :placeholder="$t('models.user.profession')">
      </div>
    </div>
    <div class="flex">
      <div class="input-100">
        <input type="text" name="schoolm"  autocomplete="off" v-model="schoolm" :placeholder="$t('models.user.schoolm')">
      </div>
    </div>
    <div class="flex">
      <div class="input-100">
        <input type="text" name="city"  autocomplete="off" v-model="city" :placeholder="$t('models.user.city')">
      </div>
    </div>
    <div class="flex">
      <div class="input-100">
        <input type="text" name="lang"  autocomplete="off" v-model="lang" :placeholder="$t('models.user.lang')">
      </div>
    </div>
    <div class="flex">
      <div class="input-100">
        <input type="password" name="password" autocomplete="off"  v-model="password" :placeholder="$t('models.user.password')">
      </div>
    </div>
    <div class="button-wrapper">
      <altai-button type="submit" :disabled="isDisabled">{{$t('components.user.account.button')}}</altai-button>
    </div>
</form>
</template>
<script>
import _ from 'lodash'
import AltaiButton from '@/components/ui/AltaiButton'
import API from '@/services/API'
import firebase from 'firebase'
import {mapMutations} from 'vuex'
import vueSlider from 'vue-slider-component';
export default {
  name: 'account-profile',
  components: {
    AltaiButton,
    vueSlider
  },
  data: function data () {
    return {
      facebook: null,
      gender: null,
      color: null,
      profession: null,
      schoolm: null,
      city: null,
      lang: null,
      password: '',
      message: '',
      invalide: false,
      valide: false,
      items: ['Male', 'Female', 'Robot'],
      ...this.$store.state.user
    }
  },
  computed: {
    isDisabled () {
      return _.isEqual(this.newUser, this.initialUser) // initial user is not updated, but user is
    },
    initialUser () {
      const user = this.$store.state.user
      console.log('initial', user);
      return {
        facebook: user.facebook || null,
        gender: user.gender || null,
        color: user.color || null,
        profession: user.profession || null,
        schoolm: user.schoolm || null,
        city: user.city || null,
        lang: user.lang || null,
      }
    },
    newUser () {
      return {
        facebook: this.facebook,
        gender: this.gender,
        color: this.color,
        profession: this.profession,
        schoolm: this.schoolm,
        city: this.city,
        lang: this.lang,
      }
    }
  },
  methods: {
    ...mapMutations(['valideUser']),
    onUpdate: function (e) {
      let $this = this
      API.user.update(this.newUser, this).then((user) => {
        // go back to the chat interface
        console.log('update user', user)
        this.$router.push({ name: 'Chat' })
      }).catch(function (error) {
        $this.invalide = true
        $this.message = error
        console.log('error',error)
      })
    }
  },
  mounted () {
    let $this = this
    firebase.auth().onAuthStateChanged(function (user) {
      if (user) {
        // store.commit('updateUserId', user.uid)
      } else {
        const userId = 123
//        $this.$router.push({ name: 'Signup', params: { userValitde: false }})
        $this.$router.push({name: 'Signup', params: { userId }})
        $this.valideUser(false)
      }
    })
  }
}
</script>
<style lang="scss">
.flex{

  :first-child{
    margin-right: 8px;
  }
  :not(:first-child){
    margin-left: 8px;
  }
  .input-100{
    width: 100%;
    margin-right: inherit;
    input {
      width: 100%;
    }

  }
  input[type=checkbox] {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;

  }
  input[type=checkbox] {
    -border-radius: 4px;
    height: 30px;
    width: 30px;
    background: linear-gradient(70deg, rgba(#aaaaaa, 0.5), rgba(#999999, 0.5));
    border: 1px solid #ccc;
  }
  input[type="checkbox"]:checked {
    background: linear-gradient(70deg, rgba(#aaaaaa, 0.5), rgba(#999999, 0.5));
    /*margin:0px;*/
    position: relative;
    &:before {
      font-family: FontAwesome;
      content: '\f00c';
      display: block;
      color: grey;
      font-size: 13px;
      position: absolute;
    }
  }
  label {
    position: relative;
    bottom: 28px;
  }

}
.flex2{
  margin-top: 65px

}
form.big.vertical .dropdown-toggle input{
  margin-top: 0;
  margin-bottom: 0;
}
.v-select .open-indicator{
  right:20px;
}
.dropdown{
  margin-top: 16px;
  margin-bottom: 16px;
  margin-right: 0 !important;
}
.dropdown .dropdown-menu li{
  margin-left: 0px;
  margin-right: 0px;
}
.v-select.searchable .dropdown-toggle{
  width: 100%;
}
</style>



